.home-page .page {
  background: whitesmoke; }

.canvas-wrapper-home {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease-in-out;
  opacity: 0; }

body.visible .canvas-wrapper-home {
  opacity: 1; }

.home-wrapper {
  width: 100%;
  height: 200%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  perspective: 1200px;
  text-align: center; }
  .home-wrapper h1 {
    font-family: Open Sans;
    font-size: 50px; }
  .home-wrapper h2 {
    font-family: Encode Sans;
    font-size: 35px; }

.tech-slider-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
  -webkit-animation: spin linear 10s infinite;
  animation: spin linear 10s infinite;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50% -300px; }
  .tech-slider-wrapper div {
    position: absolute;
    width: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform-origin: 50% 50% -300px; }
    .tech-slider-wrapper div:nth-child(1) {
      -webkit-transform: rotateY(calc(45deg * 1));
      transform: rotateY(calc(45deg * 1)); }
    .tech-slider-wrapper div:nth-child(1) {
      -webkit-transform: rotateY(calc(45deg * 1)) scale(0.8);
      transform: rotateY(calc(45deg * 1)) scale(0.8); }
    .tech-slider-wrapper div:nth-child(2) {
      -webkit-transform: rotateY(calc(45deg * 2));
      transform: rotateY(calc(45deg * 2)); }
    .tech-slider-wrapper div:nth-child(3) {
      -webkit-transform: rotateY(calc(45deg * 3));
      transform: rotateY(calc(45deg * 3)); }
    .tech-slider-wrapper div:nth-child(3) {
      -webkit-transform: rotateY(calc(45deg * 3)) scale(1.5);
      transform: rotateY(calc(45deg * 3)) scale(1.5); }
    .tech-slider-wrapper div:nth-child(4) {
      -webkit-transform: rotateY(calc(45deg * 4));
      transform: rotateY(calc(45deg * 4)); }
    .tech-slider-wrapper div:nth-child(4) {
      -webkit-transform: rotateY(calc(45deg * 4)) scale(0.8);
      transform: rotateY(calc(45deg * 4)) scale(0.8); }
    .tech-slider-wrapper div:nth-child(5) {
      -webkit-transform: rotateY(calc(45deg * 5));
      transform: rotateY(calc(45deg * 5)); }
    .tech-slider-wrapper div:nth-child(5) {
      -webkit-transform: rotateY(calc(45deg * 5)) scale(0.8);
      transform: rotateY(calc(45deg * 5)) scale(0.8); }
    .tech-slider-wrapper div:nth-child(6) {
      -webkit-transform: rotateY(calc(45deg * 6));
      transform: rotateY(calc(45deg * 6)); }
    .tech-slider-wrapper div:nth-child(7) {
      -webkit-transform: rotateY(calc(45deg * 7));
      transform: rotateY(calc(45deg * 7)); }
    .tech-slider-wrapper div:nth-child(8) {
      -webkit-transform: rotateY(calc(45deg * 8));
      transform: rotateY(calc(45deg * 8)); }
    .tech-slider-wrapper div:nth-child(8) {
      width: unset;
      -webkit-transform: rotateY(calc(45deg * 8)) scale(0.25);
      transform: rotateY(calc(45deg * 8)) scale(0.25); }
    .tech-slider-wrapper div svg {
      display: block; }

.react-logo-wrapper svg {
  width: 250px; }

@keyframes spin {
  0% {
    -webkit-transform: rotateY(0deg);
    transform: rotateY(0deg); }
  100% {
    -webkit-transform: rotateY(360deg);
    transform: rotateY(360deg); } }
