.project-container {
  min-width: 320px;
  display: flex;
  flex-direction: column;
  flex: 1 0 40%;
  height: 55vh;
  margin: 30px;
  background: white;
  transition: all 0.3s ease-in-out;
  position: relative;
  top: 0;
  border-radius: 3px;
  -webkit-box-shadow: 0px 0px 8px 0px rgba(255, 255, 255, 0.5);
  -moz-box-shadow: 0px 0px 8px 0px rgba(255, 255, 255, 0.5);
  box-shadow: 0px 0px 8px 0px rgba(255, 255, 255, 0.5); }
  .project-container:hover {
    top: -5px;
    -webkit-box-shadow: 0px 0px 20px 0px rgba(255, 255, 255, 0.75);
    -moz-box-shadow: 0px 0px 20px 0px rgba(255, 255, 255, 0.75);
    box-shadow: 0px 0px 20px 0px rgba(255, 255, 255, 0.75); }
  .project-container:last-child {
    flex: 0 0 40%; }

.img-wrapper {
  background: blue;
  justify-self: flex-start;
  width: 100%;
  height: 285px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px; }

.tags-wrapper {
  margin-top: auto; }
